.flex_container{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.menu{
  position: fixed;
  width: 25%;
  padding-top: 5%;
  padding-left: 30px;
  padding-right: 0px;
  z-index: 10;
  
}

.main_scroll{
  margin-top: 0%;
  padding-left: 30%;
  justify-content: center;
}

#Logo{
  width: 850px;
  height: 650px;
  transform: translate(300px, -450px);
}

button{
  cursor: pointer;
  background-color: white;
  border: 0;
  outline: 0;
}

a{
    color: inherit;
    text-decoration: none;
}